import Vue from 'vue'
import Router from 'vue-router'
import Cover from '@/views/Cover' 
import About from '@/views/About'
import Experience from '@/views/Experience'

Vue.use(Router)
const router=new Router({
  routes:[
    {
      path:'/cover',
      
      component:Cover
    },
    {
      path:'/about',
      component:About
    },
    {
      path:'/experience',
      component:Experience
    }
  ]
})
export default router
